const NavbarComponent = (props) => {

    const [Background, setBackground] = useState("light")
    const [Variant, setVariant] = useState("light")

    let { isLightTheme } = useContext(ThemeContext)
    function handleLogout() {
        console.log("Logout")
    }

    useEffect(() => {
        if (isLightTheme === 1) {
            // setBackground("light");
            setBackground(null);
            setVariant("light");
        } else {
            setBackground("dark");
            setVariant("dark");
        }
    }, [isLightTheme])

    return (
        <>
            <Navbar sticky="top" collapseOnSelect expand="lg" bg={Background} variant={Variant}>

                {/* <Navbar.Brand href="#home">React-Bootstrap</Navbar.Brand> */}

                {/* <div className="containery"> */}
                <Link className="navbar-brand" to="/">Lattice</Link>
                {/* onClick={this.setNavbarVars} */}
                <ThemeToggler className="show-xs show-sm hide-md ml-auto mr-0" />
                <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                <Navbar.Collapse id="responsive-navbar-nav">


                    <form className="form-inline search-box text-center m-auto">
                        <input className="form-control" type="search" placeholder="Search" aria-label="Search" />
                        <FontAwesomeIcon icon={faSearch} className="search-icon" />
                    </form>
                    <Nav className="ml-auto">
                        {/* <Link to="/" className="nav-link">
                            Home
                        </Link> */}
                        <ThemeToggler className="hide-xs hide-sm show-md" />

                        <NavLink to="/dashboard" className="nav-link">
                            Dashboard
                        </NavLink>

                        <NavLink to="/about" className="nav-link">
                            About
                        </NavLink>

                        <NavDropdown title="Dropdown" id="collasible-nav-dropdown">

                            <NavDropdown.Item as={Link} to="/settings">Settings</NavDropdown.Item>
                            <NavDropdown.Item as={Link} to="/login">Login</NavDropdown.Item>
                            <NavDropdown.Item as={Link} to="/register">Register</NavDropdown.Item>

                            <NavDropdown.Divider />
                            <NavDropdown.Item href="#logout" onClick={handleLogout}>Logout</NavDropdown.Item>
                        </NavDropdown>
                    </Nav>

                </Navbar.Collapse>
                {/* </div> */}
            </Navbar>


        </>
    );
}

export default NavbarComponent;