import React, { useState } from 'react';

import { Link } from "react-router-dom"

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCommentAlt, faBookmark, faThumbsUp } from '@fortawesome/free-regular-svg-icons'
import { faEllipsisV, faThumbsUp as solidUp, faBookmark as solidBookmark } from '@fortawesome/free-solid-svg-icons'
import Loader from "./loader"

function setLikeDisp(liked) {
    let likeDisp
    if (!liked) {
        likeDisp = (
            <FontAwesomeIcon icon={faThumbsUp} className="action-icon" />
        )
    } else {
        likeDisp = (
            <FontAwesomeIcon icon={solidUp} className="action-icon" />
        )
    }

    return likeDisp;
}

function setBookmarkDisp(bookmarked) {
    let bkDisp
    if (!bookmarked) {
        bkDisp = (
            <FontAwesomeIcon icon={faBookmark} className="action-icon" />
        )
    } else {
        bkDisp = (
            <FontAwesomeIcon icon={solidBookmark} className="action-icon" />
        )
    }

    return bkDisp;
}


function Card(props) {
    console.log({ card_props: props })

    // const [comment, setComment] = useState(initialState)

    const [liked, setLiked] = useState(false);
    const [bookmarked, setBookmarked] = useState(false);

    let bookmarkDisp = setBookmarkDisp(bookmarked)
    let likeDisp = setLikeDisp(liked)

    function handleLike(e) {
        setLiked(!liked)
        likeDisp = setLikeDisp(liked)
    }

    function handleBookmark(e) {
        setBookmarked(!bookmarked)
        bookmarkDisp = setBookmarkDisp(bookmarked)
    }

    let template;


    if (props.type === 1) {
        let style = {
            backgroundImage: `url(${props.data.bannerLink})`,
            // backgroundSize: "cover",
            backgroundPosition: "50% 50%",
            // backgroundOverflow: "visible",
            // backgroundPosition:"50% 50%",
        }
        template = (
            <div className={props.className}>
                <div className="custom-card">
                    <div className="row">

                        <div className="col-md-3 img-col-div  d-inline-block"  >
                            <div className="img-c pr-0 mr-0" style={style} ></div>
                        </div>

                        <div className="col-md-9 card-content d-inline-block mt-2">
                            <div className="content-box">
                                <div className="content-b2 mb-auto">
                                    <h3 className="title">{props.data.title}</h3>
                                    <p className="time">{props.data.time}</p>
                                    <p className="content">{props.data.content}</p>
                                    <p className="tags">{props.data.tags}</p>
                                </div>

                                <hr className="light w-100 p-0 m-0 mt-auto" />
                                <div className="user-info-c w-100 mt-2 mb-0">
                                    <div className="user-image-c hide-xs show-sm">
                                        <Link className="unset" to="/">
                                            <img src={props.data.user.imageLink} alt="user" className="user-image" />
                                        </Link>
                                    </div>

                                    <div className="user-info">
                                        <Link className="unset" to="/">
                                            <span className="u-name d-block">{props.data.user.name}</span>
                                            <span className="u-username d-inline-block">@{props.data.user.username}</span>
                                        </Link>
                                    </div>

                                    <div className="ml-auto u-actions">
                                        <span className="action-icon-c" onClick={handleBookmark}>
                                            {bookmarkDisp}
                                        </span>
                                        <span className="action-icon-c">
                                            <FontAwesomeIcon icon={faCommentAlt} className="action-icon" />
                                        </span>
                                        <span className="action-icon-c" onClick={handleLike}>
                                            {likeDisp}
                                        </span>
                                        <span className="action-icon-c mr-4">
                                            <FontAwesomeIcon icon={faEllipsisV} className="action-icon" />
                                        </span>
                                    </div>
                                </div>

                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );

    } else if (props.type === 2) {
        template = (
            <>
                <div className={props.className}>
                    <div className="custom-card">
                        {/* <div className="row"> */}

                        <div className="col-md-12s card-content d-inline-block mt-2">
                            <div className="content-box pr-4 pl-4 pb-4 pt-2">
                                <div className="content-b2 mb-auto">
                                    <h3 className="title">{props.data.title}</h3>
                                    <p className="time">{props.data.time}</p>
                                    <p className="content">{props.data.content}</p>
                                    <p className="tags">{props.data.tags}</p>
                                </div>

                                <hr className="light w-100 p-0 m-0 mt-auto" />
                                <div className="user-info-c w-100 mt-2 mb-0">
                                    <div className="user-image-c hide-xs show-sm">
                                        <Link className="unset" to="/">
                                            <img src="https://via.placeholder.com/100x100" alt="user" className="user-image" />
                                        </Link>
                                    </div>

                                    <div className="user-info">
                                        <Link className="unset" to="/">
                                            <span className="u-name d-block">name</span>
                                            <span className="u-username d-inline-block">@username</span>
                                        </Link>
                                    </div>

                                    <div className="ml-auto u-actions">
                                        <span className="action-icon-c" onClick={handleBookmark}>
                                            {bookmarkDisp}
                                        </span>
                                        <span className="action-icon-c">
                                            <FontAwesomeIcon icon={faCommentAlt} className="action-icon" />
                                        </span>
                                        <span className="action-icon-c" onClick={handleLike}>
                                            {likeDisp}
                                        </span>
                                        <span className="action-icon-c mr-4">
                                            <FontAwesomeIcon icon={faEllipsisV} className="action-icon" />
                                        </span>
                                    </div>
                                </div>

                            </div>
                        </div>
                        {/* </div> */}
                    </div>
                </div>
            </>
        )

    } else if (props.type === 3) {
        let style = {
            background: "yellow",
            height: "250px",
            textAlign: "center",
            verticleAlign: "middle",
            // padding: "auto",
            justifyContent: "center",
            alignItems: "center",
            // padding: "70%"
            // backgroundSize: "cover",
            // backgroundPosition: "50% 50%",
            // backgroundOverflow: "visible",
            // backgroundPosition:"50% 50%",
        }

        let style2 = {
            // top: "50% !important",
            transform: "rotateZ(-90deg)",
            textAlign: "center",
            verticleAlign: "middle",
            marginTop: "128px",
            // margin: "auto",
            // padding: "auto"
        }
        template = (
            <div className={props.className}>
                <div className="custom-card">
                    <div className="row">

                        <div className="col-md-2 d-inline-block"  >
                            <div className="img-c hide-xs hide-sm show-md" style={style} >
                                <h1 className="text-banner" style={style2}>DISCUSS</h1>
                            </div>
                        </div>

                        <div className=" col-md-10 card-content d-inline-block mt-2">
                            <div className="content-box">
                                <div className="content-b2 mb-auto">
                                    <h3 className="title">Title</h3>
                                    <p className="time">3 hours ago</p>
                                    <p className="content">
                                        Lorem ipsum dolor sit, amet consectetur adipisicing elit. Praesentium dignissimos repellat officiis sunt iure nihil tenetur accusantium. Obcaecati sit impedit quas voluptate sapiente omnis laboriosam voluptatum aliquid cupiditate pariatur! Ex?
                                        </p>
                                    <p className="tags">#tag1 #tag2</p>

                                </div>

                                <hr className="light w-100 p-0 m-0 mt-auto" />
                                <div className="user-info-c w-100 mt-2 mb-0">
                                    <div className="user-image-c hide-xs show-sm">
                                        <Link className="unset" to="/">
                                            <img src="https://via.placeholder.com/100x100" alt="user" className="user-image" />
                                        </Link>
                                    </div>

                                    <div className="user-info">
                                        <Link className="unset" to="/">
                                            <span className="u-name d-block">name</span>
                                            <span className="u-username d-inline-block">@username</span>
                                        </Link>
                                    </div>

                                    <div className="ml-auto u-actions">
                                        <span className="action-icon-c" onClick={handleBookmark}>
                                            {bookmarkDisp}
                                        </span>
                                        <span className="action-icon-c">
                                            <FontAwesomeIcon icon={faCommentAlt} className="action-icon" />
                                        </span>
                                        <span className="action-icon-c" onClick={handleLike}>
                                            {likeDisp}
                                        </span>
                                        <span className="action-icon-c mr-4">
                                            <FontAwesomeIcon icon={faEllipsisV} className="action-icon" />
                                        </span>
                                    </div>
                                </div>

                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );

    } else if (props.type === 4) {
        template = (
            <>
                <div className={props.className}>
                    <div className="custom-card ml-0 pl-0 pt-4 pb-4">
                        <br />
                        <br />
                        <br />
                        <Loader class="m-4 p-4" />
                        <br />
                        <br />
                        <br />
                    </div>
                </div>
            </>
        )

    } else {
        template = (
            <>No type</>
        )
    }
    return template;
}


export default Card;